import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { contactInfoComponent } from "./contactInfo.component"
import {MdButtonModule, MdCheckboxModule, MdInputModule,MdIconModule} from '@angular/material';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {NoopAnimationsModule} from '@angular/platform-browser/animations';
import { FormsModule,  ReactiveFormsModule } from "@angular/forms";
import { Service } from './../../data/service';
@NgModule({
  declarations: [
    contactInfoComponent,
    ],
  imports: [
    MdButtonModule,
    MdIconModule,
    ReactiveFormsModule,
   MdInputModule,
    BrowserModule,
    BrowserAnimationsModule,
    NoopAnimationsModule,
    FormsModule
  ],
  providers: [Service],
  exports :[contactInfoComponent, MdInputModule],
  bootstrap: [contactInfoComponent]
})
export class contactInfoModule { }



