import { Injectable } from '@angular/core';
import { Http, Headers, RequestOptions } from '@angular/http';

import { IContacts, Contact } from "./contact"
import 'rxjs/add/operator/toPromise';

@Injectable()
export class Service {
   // private apiUrl = "api/contacts";

  //  private apiUrl = "http://localhost:8080";
   private apiUrl = "";
   testContacts : Contact[]=[];                       
    header = new Headers ({'Content-Type':'application/json'});
     options= new RequestOptions({headers:this.header});
    constructor (private http: Http) {
       
    }
    getContacts() : Promise<Contact[]> {
        
        return (this.http.get(`${this.apiUrl}/contact/getAll` )
                        .toPromise()
                        .then(res=>{ console.log( res.json() );  return res.json()  } )
                        .catch( err =>{ console.log( err ) }  ));
                         

    }
    addNewContact (contact:IContacts) : Promise<IContacts> {
    
    
     return this.http.post(`${this.apiUrl}/contact/add`,
     JSON.stringify(contact), this.options)
     .toPromise()
     .then(res=>res.json()  )
     
    }

    update (contact:IContacts) :Promise<IContacts> {
        console.log("Update final");
  
   return this.http.post(`${this.apiUrl}/contact/update`,
     JSON.stringify(contact), this.options)
     .toPromise()
     .then(res=>res.json()  )
    }

    delete (contact:IContacts) :Promise<IContacts> {
        console.log("delete final");
  
     return this.http.post(`${this.apiUrl}/contact/delete`,
     JSON.stringify(contact), this.options)
     .toPromise()
     .then(res=>res.json()  )
    }
  
    private sortContacts(a:IContacts, b:IContacts) {
        if (a.name[0]<b.name[0])
            return -1;
            if (a.name[0]>b.name[0])
            return 1;
            else return 0;
    }
    private handleError(error) {
        console.error(error);
        return Promise.reject(error.message || error)
    }
}


