import { Component, ViewChild } from '@angular/core';
import { Service } from "./data/service";
import { Contact } from "./data/contact";
import { contactInfoComponent } from './components/contact-info/contactInfo.component';
import { contactList }  from './components/contact-list/contactList.component'
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  providers : [Service ],
  
})

export class AppComponent {
  showTheInfoComponent :boolean = false;
  sellectedContact:Contact=null;
  @ViewChild (contactInfoComponent) contactInfo :contactInfoComponent;
  @ViewChild (contactList) contaclist:contactList;
  constructor() {

  
  }
  closeInfo() {
    console.log("close");
    this.showTheInfoComponent=false;

  }
  selectContact(contact:Contact)
  {
    this.sellectedContact=contact;
    if (!this.showTheInfoComponent)

    {this.showTheInfoComponent=true; console.log("t");return;}
    
    this.contactInfo.changed(contact);
  }
  addNewContact() {
   
  
    this.sellectedContact=null; 
    if (!this.showTheInfoComponent)
    {this.showTheInfoComponent=true; console.log("t");return;}
    this.contactInfo.changed(this.sellectedContact);
  }
  updateContact(contact: Contact) {
    console.log("update 1");
   this.contaclist.update(contact);
   this.closeInfo();
  }
   afterNewContactAdded (newContact:Contact) {
     console.log(newContact);
       this.contaclist.onAddNewContact(newContact);
        this.contaclist.getAll();
        this.showTheInfoComponent=false;
    }
   
}



