import { Component , Input, OnInit, Output, EventEmitter} from "@angular/core";
import { Contact } from "../../data/contact";
import {FormControl, Validators} from '@angular/forms';
import { Service } from './../../data/service';
const EMAIL_REGEX = /^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
 
@Component({
    selector :"contact-info",
    templateUrl: "./contactInfo.component.html",
    styleUrls: ["./contactInfo.component.css"]
})

export class contactInfoComponent implements OnInit{
    emailFormControl = new FormControl('', [
    Validators.required,
    Validators.pattern(EMAIL_REGEX)]);
    
    @Input() sellectedContact:Contact;
    @Output() onNewContactAdded= new EventEmitter();
    @Output() onClose= new EventEmitter();
    @Output() onContactUpdate= new EventEmitter();
    IsNewContact : Boolean;
    contactName : string;
    contactPhoneNumber:string;
    contactEmailAdress : string;
    constructor (private  service :Service) {
    
    }
    changed(contact :Contact){
             this.sellectedContact=contact;
               this.setProperty();
        }
    
    ngOnInit(){
      this.setProperty();
    }
       SaveContact() {
        console.log('saveContact');
        if (!this.contactName  || !this.contactPhoneNumber || !this.contactEmailAdress)
        {
            alert("Fill the empty fields");
            return;
        }
        
        if (this.IsNewContact){
           let newContact = new Contact(0,this.contactName,this.contactPhoneNumber,this.contactEmailAdress);
           this.onNewContactAdded.emit(newContact);
           return;
        } else {
            let changedContact = new Contact(this.sellectedContact.id,this.contactName,this.contactPhoneNumber,this.contactEmailAdress);
            this.onContactUpdate.emit(changedContact);
        }
        
    }  
    setProperty() {
     if (this.sellectedContact==null) {
               this.contactName="";
               this.contactPhoneNumber="";
               this.contactEmailAdress="";
               this.IsNewContact=true;
           }   else{
               this.contactName=this.sellectedContact.name;
               this.contactPhoneNumber=this.sellectedContact.phoneNumber;
               this.contactEmailAdress=this.sellectedContact.emailAdress;
               this.IsNewContact=false;
           }
    }
    close() {
        console.log("close  1");
        this.onClose.emit();
    }
              
}


