import { Component , Input,OnInit, Output, EventEmitter} from "@angular/core";
import { Contact } from "../../data/contact";
import { Service } from "../../data/service";


@Component({
    moduleId:module.id,
    selector :"contact-list",
    templateUrl: "contactList.component.html",
    styleUrls: ["contactList.component.css"],
    providers:[ Service]
    
})

export class contactList implements OnInit {
  @Output() onContactSellected= new EventEmitter();
  @Output() onNewContact = new EventEmitter();
  @Output() onDelete = new EventEmitter();
  public contacts :Contact[];
  private _contacts:Contact[];
   constructor ( private service: Service )
   {
     
          
   }
   onclick (contact:Contact, event) {
     console.log(event);
     
     
     this.onContactSellected.emit(contact);
   }
   onButtonClick(){
     this.onNewContact.emit();
   }
    ngOnInit() {
       
     this.getAll();
    }
     getAll() {
     this.service.getContacts()
      .then(con=>{ console.log( con);  this.contacts=con; this._contacts=con;  } )
      .catch( error => { "EROROOOOR" } ) ;
    }
    onAddNewContact(testContact){
     // let testContact :Contact = new Contact(1,"name","+11","@das");
      this.service.addNewContact(testContact)
      .then( data => { console.log(data); this.getAll()} )
      .catch();

    }

    update(contact) {
      this.service.update(contact)
      .then( data => { console.log(data); this.getAll()} )
      .catch();
    }
    delete(contact) {
     
      this.service.delete(contact)
      .then( data => { console.log(data); this.getAll(); this.onDelete.emit();} )
      .catch();   
      
    }
   private searchByQuerry (event :any){
       
       let querry= event.target.value;
       this.contacts=this._contacts.filter((element)=>{
         return element.name.toLowerCase().indexOf(querry.toLowerCase())!==-1;
      });
     
      }
   
    }



